// container module
// 

.sign-container {
    @extend .list-container;

    background-color: $color-234;
    height: 410px;
    // height: 400px;
    // min-height: 100%;
    // height: 100vh;
}

.card-container {
    @extend .list-container;
    background-color: $color-fff;
    padding: 15px;

    .title-box {
        padding: 15px;
        background-color: #6ecaf9;
        color: $color-fff;
        font-size: 25px;

        margin-left: 5px;
        margin-right: 5px;
        margin-bottom: 15px;

        border-radius: 5px;
    }

    &:after {
        content: "";
        clear: both;
        display: block;
    }

    .card {
        height: 400px;
        background-color: #f8f8f8;
        padding: 15px;
        margin: 15px 5px;
        text-align: center;
        border-top: 6px solid #6d97ab;
        border-radius: 3px;

        &:hover {
            box-shadow: 0 0 4px #888;
        }

        .author {
            display: block;
            // background-color: $color-eee;
            margin-bottom: 15px;

            .avatar {

                background-color: #6d97ab;
                display: inline-block;
                border: 1px solid #888;
                height: 80px;
                width: 80px;
                border-radius: 50%;
                // margin-top: -80px 0 10px;
            }
        }

        .follow-btn {
            background-color: $color-42c;
            display: inline-block;
            color: $color-fff;
            padding: 10px ($padding-size + 15px);
            border-radius: 18px;
            font-size: 16px;

            margin-bottom: 15px;
        }

        .recent-update {
            // background-color: $color-eee;
            text-align: center;
            padding: 15px 0;

            .new {
                display: block;
                // background-color: #FFF;
                margin-bottom: 5px;
            }
        }
    }
}